        color: #fff !important;
        pointer-events: none;
    }
}


.burger-btn{
    background: none;
    border: none;
    outline: none;
}
.burger-btn svg{
    display: block;
    width: 20px;
    height: 20px;
}
.hamburger{
    padding: 5px;
    width: 20px;
    height: 20px;
    padding: 2px;
   
    .hamburger-box{
        width: 100%;
        height: 100%;
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-end;
        .hamburger-inner{
            display: block;
            width: 12px;
            height: 2px;
            background: #000;
            transition: 0.2s linear;
            &::before{
                content: '';
                width: 15px;
                height: 2px;
                background: #000;
                top: 0px;
                right: 0;
                display: block;
                position: absolute;
                transition: 0.2s linear;
                
            }
            &::after{
                content: '';
                width: 15px;
                height: 2px;
                background: #000;
                bottom: 0px;
                right: 0;
                display: block;
                position: absolute;
                transition: 0.2s linear;
            }
        }
    }

    &:not(.collapsed){
        .hamburger-inner{
            width: 0;
            &::before{
                transform: rotate(45deg);
                top: 7px;
            }
            &::after{
                transform: rotate(-45deg);
                bottom: 7px;
            }
        }
    }
}

.nav-link{
    &:hover, &.show{
        color: #A8811D !important;
        text-underline-offset: 3px;
    }

}

