            }
        }

        svg{
            width: 36px;
            height: 36px;
            border-radius: 50%;
        }
    }
    .payment__attention{
        display: flex;
        align-items: center;
        gap: 14px;
        svg{
            min-width: 40px;
            min-height: 40px;
            max-width: 40px;
            max-height: 40px;
        }
