        font-size: 12px;
        line-height: 150%;
        color: rgba(0, 0, 0, 0.6);
        margin-bottom: 25px;
        @media(max-width: 480px){
            br{
                display: none;
            }
        }
    }
    .payment__buttons{
        display: flex;
        gap: 20px;
        @media(max-width: 1400px){
            gap: 10px;
        }
        .btn{
            border-radius: 16px;
            padding: 14px 24px;
