.modal {
    opacity: 0;
    visibility: hidden;
    display: block !important;
    transition: 0.3s linear;
    .modal-content{
        transition: 0.4s linear;
        transform: translateY(50px);
        opacity: 0;
    }
    &.show{
        opacity: 1;
        visibility: visible;
        transition: 0.1s linear;
        .modal-content{
            transform: translateY(0px);
            opacity: 1;
            transition: 0.2s linear;
        }
    }
}


