        position: absolute;
        top: 4px;
        left: 0;
        height: 100%;
        pointer-events: none;
        transform-origin: 0 0;
        transition: 0.1s linear;
        padding: 13px 10px 13px 20px;
        font-weight: 400;
        font-size: 14px;
        line-height: 100%;
        display: flex;
        align-items: center;
        color: rgba(0, 0, 0, 0.4);
        height: max-content;
        display: flex !important;
        align-items: center;
        max-height: 40px;
    }

  

    input:focus{
        & ~ label{
            font-size: 10px;
            top: 3px;
            left: 17px;
            padding: 0 5px;
            
        }
    }

    input.filled{
        & ~ label{
            font-size: 10px;
            top: 3px;
            left: 17px;
            padding: 0 5px;
            
        }
    }

   

    input::placeholder{
        color: transparent;
    }

  
  
  }


  .currency-input.is-invalid{
    border: 1.5px solid #B72025;
    padding-right: calc(1.5em + .75rem);
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 12 12' width='12' height='12' fill='none' stroke='%23dc3545'%3e%3ccircle cx='6' cy='6' r='4.5'/%3e%3cpath stroke-linejoin='round' d='M5.8 3.6h.4L6 6.5z'/%3e%3ccircle cx='6' cy='8.2' r='.6' fill='%23dc3545' stroke='none'/%3e%3c/svg%3e");
    background-repeat: no-repeat;
    background-position: right calc(.375em + .1875rem) center;
    background-size: 20px 20px;
    position: relative;
    .currency-input__error{
        position: absolute;
        font-weight: 500;
        font-size: 11px;
        line-height: 100%;
        color: #B72025;
        left: 20px;
        bottom: -32px;
    }
  }

  .currency-input.valid{
    border: 1.5px solid #8AC988;
    padding-right: calc(1.5em + .75rem);
    background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9.99999 15.172L19.192 5.979L20.607 7.393L9.99999 18L3.63599 11.636L5.04999 10.222L9.99999 15.172Z' fill='%238AC988'/%3E%3C/svg%3E%0A");
    background-repeat: no-repeat;
